import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-home-page',
  templateUrl: './home-page.component.html',
  styleUrls: ['./home-page.component.css', './button-styles.css']
})
export class HomePageComponent implements OnInit {

  public elements: Element[] = new Array<Element>();
  public groups: ElementGroup[] = new Array<ElementGroup>();
  public periods: ElementPeriod[] = new Array<ElementPeriod>();

  constructor() {
    for (let index = 1; index < 19; index++) {
      this.groups.push(new ElementGroup(index.toString()));

    }

    for (let index = 1; index < 8; index++) {
      this.periods.push(new ElementPeriod(index.toString()));

    }
  }

  ngOnInit() {
  }

}

export class Element {
  constructor(
    public group: number,
    public period: number
  ) { }
}

export class ElementGroup {
  constructor(
    public name: string
  ) { }
}

export class ElementPeriod {
  constructor(
    public name: string
  ) { }
}